.pictures {
  background: #fff;
  border-radius: 2px;
  height: 150px;
  margin: 1rem;
  position: relative;
  width: 150px;
  box-shadow: 0 3px 6px #999, 0 3px 6px #999;
  text-align: center;
  float: left;
}

 .pictures > .img-container {
  height: 100%;
  text-align: center;
  background:#6CADDC;
}


.pictures > .img-container > img {
  width: 150px;
  height: 150px;
 
 
} 


.title {
  width: 100%;
  text-align: center;
  /* font-size: 3rem; */
  color: white;
  padding-top: 100px;
  margin-bottom: 0px;
  margin-bottom: 10px;
  border-radius: 0px 0px 25px 25px;
}

h1 {
  padding-bottom: 20px;
  margin-bottom: 0px;
  font-size: 40px;
  
}

h2 {
  padding-bottom: 30px;
  font-size: 25px;
}
.navbar {
  background: orangered;
  /* min-height: 70px; */
  height: 70px;
  position: fixed;
  width: 100%;
  box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
  color: white;
  font-size: calc(10px + (24 - 10) * (100vw - 320px) / (1200 - 400));
  z-index: 99;
  white-space: nowrap;

 
}

.navbar ul {
  margin: 0;
  padding: 0;
  
 
}

.navbar li {
  display: inline-block;
  padding: 26px 0px;
  width: 33.3%;
  text-align: center;
  margin-right: 100px;
}

.navbar a {
  text-decoration: none;
  color: white;
  font-weight: 900;
}

.otherNav {
  margin-bottom: 30px;
}

@media (max-width: 420px) {
  .navbar li:first-child {
    width: 25%;
  }

  .navbar li:nth-child(2) {
    width: 35%;
  }

  .navbar li:last-child {
    width: 40%;
  }
}
.navbar .correct {
  -webkit-animation: .4s correct;
          animation: .4s correct;
}

.navbar .incorrect {
  -webkit-animation: .4s incorrect;
          animation: .4s incorrect;
}

@-webkit-keyframes correct {
  0% {
    color: white;
  }

  50% {
    color: #2e7d32;
  }

  100% {
    color: white;
  }
}

@keyframes correct {
  0% {
    color: white;
  }

  50% {
    color: #2e7d32;
  }

  100% {
    color: white;
  }
}

@-webkit-keyframes incorrect {
  0% {
    color: white;
  }

  50% {
    color: #c62828;
  }

  100% {
    color: white;
  }
}

@keyframes incorrect {
  0% {
    color: white;
  }

  50% {
    color: #c62828;
  }

  100% {
    color: white;
  }
}

.footer {
  min-height: 100px;
  /* background: #E5E5E5; */
  position: relative;
  color: white;
  border-radius: 25px 25px  0px 0px;
  

 

}

.footer span {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  padding: 3px;
  padding-left: 30px;
  background: orangered;
  font-size: calc(5px + (24 - 10) * (100vw - 320px) / (1200 - 400));
  box-shadow:  0px -14px 28px rgba(0, 0, 0, 0.25), 0px -10px 10px rgba(0, 0, 0, 0.22);

  font-weight: 900;

}



